	data() {
		return {
			dis: "111"
		};
	},
	methods:{
		getUserInfo(e) {
			console.log(e)
			//  授权用户信息
			// 			this.userIcon = e.detail.userInfo.avatarUrl
			// 			this.userName = e.detail.userInfo.nickName
			let userInfo = e.detail.userInfo;
			uni.setStorageSync('userInfo', userInfo);
			if(e.detail.errMsg==="getUserInfo:ok"){
				uni.reLaunch({
					
					url:"/pages/near/near"
				})
			}
		}
	}
	
};
